import React from "react";
import Project from "../components/Project";
import projects from "../projects.json";
import AudiophileScreenshot from "../assets/images/audiophile-screenshot.png"
import BookSearchScreenshot from "../assets/images/book-search-screenshot.png"
import EmployeeDirectoryScreenshot from "../assets/images/employee-directory-screenshot.png"
import CoronaScreenshot from "../assets/images/corona-screenshot.png"
import JoyneyScreenshot from "../assets/images/joyney-screenshot.png"
import EmployeeTrackerScreenshot from "../assets/images/employee-tracker-screenshot.png"
import EmployeeTrackerDemo from "../assets/mp4s/employee-tracker-demo.mp4"

const screenshots = {
    "Audiophile" : AudiophileScreenshot,
    "Book Search" : BookSearchScreenshot,
    "Employee Directory" : EmployeeDirectoryScreenshot,
    "Corona Crisis Center" : CoronaScreenshot,
    "Joyney" : JoyneyScreenshot,
    "Employee Tracker" : EmployeeTrackerScreenshot
};

const mp4s = {
    "Employee Tracker" : EmployeeTrackerDemo
};

function Portfolio() {
    return (
        <div className="container main-content">
            <div className="row">
                <div className="col-12">
                    <h1>Portfolio</h1>
                </div>
            </div>
            <div className="row">
                {projects.map(proj => 
                   <Project
                    name={proj.name}
                    screenshot={screenshots[proj.name]}
                    deployed={proj.name === "Employee Tracker" ?
                        mp4s[proj.name] :
                        proj.deployed}
                    repo={proj.repo}
                    key={proj.name} />
                )}
            </div>
        </div>
    );
    
}

export default Portfolio;
